html {
    width: 100%;
    height: 100%;
    display: table;
    font-family:"Museo Sans";
    letter-spacing: 1px;
    font-size: 1rem;
}

body {
    width: 100%;
    height: 100%;
    /* display:table-cell; */
}

html, body {
    margin: 0px;
    padding: 0px;
}
#root{
    width: 100%;
    height: 100%;
}

@font-face {
    font-family:"Museo Sans";
    src: url("./components/fonts/museoSans.ttf") format("TrueType");
  }

  @media(min-width: 501px) {
    
    .normalNav{ display: inline-block};
    .link{ display: inline-block };
    .hamburgerMenu{ display: none; };
}

@media(max-width: 500px) {
    .link{ display: none }
    .normalNav{ display: none; };
    .hamburgerMenu{ display: inline-block; }
}